@import (less) "../node_modules/jstree/dist/themes/default/style.css";

.jstree-container-ul,
.jstree-container-ul li {
    width: 100%
}

.jstree-container-ul {
    overflow: hidden
}

.unit-info {
    width: 370px;
    position: fixed;
    height: 50px;
    top: 0;
    background-color: #F5F5F5;
    border-right: 1px solid #DDD
}

.unit-info h3 {
    text-overflow: ellipsis;
    white-space: nowrap;
    margin: 0;
    margin-top: 13px;
    text-align: center;
    overflow: hidden;
    height: 100%
}

.menu-controls:hover {
    background-color: #DDD
}

.menu-controls i {
    position: absolute;
    top: 45%;
    margin-left: 5px
}

.menu-controls {
    cursor: pointer;
    width: 30px;
    position: fixed;
    height: 100%;
    background-color: #F1F1F1;
    border-right: 1px solid #DDD;
    left: 370px;
    top: 0
}

.js-tree-menu {
    top: 50px;
    width: 370px;
    position: fixed;
    height: ~"calc(100% - 50px)";
    overflow-y: auto;
    background-color: #F5F5F5;
    border-right: 1px solid #DDD;
    display: block
}

.standalone-content-wrapper {
    margin-left: 400px
}

.standalon-page-nav hr {
    margin-top: 15px
}

.back-to-top {
    position: fixed;
    bottom: 15px;
    right: 15px;
    cursor: pointer
}

.standalon-page-nav i {
    position: fixed;
    top: 15px;
    z-index: 9999;
    cursor: pointer
}

.standalon-page-nav i:hover,
.back-to-top:hover {
    color: #666
}

.standalon-page-nav i:first-child {
    right: 15px
}

.standalon-page-nav i:last-child {
    right: 55px
}

.standalone-page-title {
    float: left;
    margin: 0;
    margin-bottom: -9px;
    margin-top: 15px
}

.unit-info i {
    display: none
}

@media(max-width:1570px) {
    .standalone-menu {
        position: relative;
    }
    .unit-info i {
        display: block;
        float: right;
        margin-right: 15px;
        cursor: pointer;
        margin-top: 8px
    }
    .unit-info {
        position: relative;
        width: 100%;
        border: 1px solid #DDD;
        cursor: pointer;
        left: 0px !important;
        z-index: 9999;
    }
    .unit-info:hover {
        background-color: #DDD
    }
    .unit-info h3 {
        margin-left: 15px;
        margin-top: 0;
        padding-top: 13px;
        text-align: left;
        float: left
    }
    .menu-controls {
        display: none
    }
    .js-tree-menu {
        width: 100%;
        max-height:0px;
        top: 0px;
        position: relative;
        border: 0;
        left: 0px !important;
        z-index: 1111;

    }
    .standalone-content-wrapper {
        width: 100%;
        margin-left: 0!important
    }

    .standalon-page-nav {
        position: fixed;
        bottom: 0;
        left: 0;
        height: 50px;
        z-index: 9999;
        width: 100%;
        background-color: #F1F1F1;
        border-top: 1px solid #DDD
    }
    .standalon-page-nav i {
        position: absolute;
        top: 10px;
        z-index: 9999;
        width: 30px;
        cursor: pointer
    }
    .standalon-page-nav i:first-child {
        left: 55px
    }
    .standalon-page-nav i:last-child {
        left: 15px
    }
    .back-to-top {
        bottom: 8px;
        z-index: 9999
    }
}
